// src/app/test/page.tsx
'use client'
import { useState } from 'react'
import { addRecipe, updateRecipe, deleteRecipe } from '@/lib/api'

export default function TestPage() {
    const [result, setResult] = useState<string>('')
    const [error, setError] = useState<string>('')

    const runTests = async () => {
        try {
            setResult('Starting tests...\n')

            // Setup test data
            setResult(prev => prev + '\nSetting up test data...')
            await fetch('/api/test-setup')
            setResult(prev => prev + '\nTest data setup completed\n')

            // Test 1: Add recipe
            setResult(prev => prev + '\nAdding recipe...')
            const newRecipe = await addRecipe({
                candleId: 1,
                materialId: 1,
                amountUsed: 150
            })
            setResult(prev => prev + `\nRecipe added successfully: ${JSON.stringify(newRecipe, null, 2)}\n`)

            // Test 2: Update recipe
            setResult(prev => prev + '\nUpdating recipe...')
            const updatedRecipe = await updateRecipe(newRecipe.id, {
                amountUsed: 175
            })
            setResult(prev => prev + `\nRecipe updated successfully: ${JSON.stringify(updatedRecipe, null, 2)}\n`)

            // Test 3: Delete recipe
            setResult(prev => prev + '\nDeleting recipe...')
            const deleteResult = await deleteRecipe(newRecipe.id)
            setResult(prev => prev + `\nRecipe deleted successfully: ${JSON.stringify(deleteResult, null, 2)}\n`)

            setResult(prev => prev + '\nAll tests completed successfully!')
        } catch (err) {
            setError(`Error: ${err.message}`)
        }
    }

    return (
        <div className="p-4">
            <h1 className="text-2xl font-bold mb-4">Recipe Operations Test</h1>

            <button
                onClick={runTests}
                className="bg-blue-500 text-white px-4 py-2 rounded hover:bg-blue-600 mb-4"
            >
                Run Tests
            </button>

            {error && (
                <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded mb-4">
                    {error}
                </div>
            )}

            {result && (
                <pre className="bg-gray-100 p-4 rounded whitespace-pre-wrap">
                    {result}
                </pre>
            )}
        </div>
    )
}
